import { createAsyncThunk, createSlice, PayloadAction } from "@reduxjs/toolkit";
import axios from "axios";
import { Cookies } from "react-cookie";
const cookie = new Cookies();
export interface InitialState {
    isLoading: boolean;
    status?: string;
    sessionId?: string;
}

const initialState: InitialState = {
    isLoading: false,
    status: "",
};

export const asyncLogin = createAsyncThunk(
    "loginSlice/asyncLogin",
    async (arg: { id: string; pw: string; sessionId?: "" }) => {
        try {
            const result = await axios.post(
                "http://localhost:5050/users/login",
                {
                    userId: arg.id,
                    userPw: arg.pw,
                    sessionId: arg.sessionId,
                },
                {
                    withCredentials: true,
                },
            );
            alert("로그인성공");
            console.log(result.data);
            return result.data;
        } catch (error) {
            console.error(error);
        }
    },
);

const loginSlice = createSlice({
    name: "login",
    initialState,
    reducers: {
        addCookie: (state, action: PayloadAction) => {
            state.sessionId = cookie.get("user");
        },
    },
    extraReducers(builder) {
        builder
            .addCase(asyncLogin.pending, (state, action) => {
                state.isLoading = true;
            })
            .addCase(
                asyncLogin.fulfilled,
                (state, { payload: { error, sessionId } }) => {
                    if (error) {
                        console.error(error);
                    } else {
                        state.isLoading = false;
                        state.sessionId = sessionId;
                        cookie.set("user", sessionId, { path: "/" });
                    }
                },
            )
            .addCase(asyncLogin.rejected, (state, { payload: error }) => {
                console.error(error);
            });
    },
});

export const { addCookie } = loginSlice.actions;
export default loginSlice.reducer;
