import { Routes, Route } from "react-router-dom";
import HeaderNavbar from "./components/HeaderNavbar";
import Main from "./page/main/Main";
import Footer from "./components/Footer";
import Login from "./page/login/Login";
import GlobalStyle from "./components/GlobalStyle";
import Register from "./page/register/Register";
import Artists from "./page/artists/Artists";
import Archive from "./page/archive/Archive";
import Inspiration from "./page/inspiration/Inspiration";
import TestPage from "./page/test/TestPage";

import { useAppDispatch, useAppSelector } from "./hook";
import { asyncAuth } from "./slices/auth";
import { Cookies } from "react-cookie";
import { useEffect } from "react";
// import { addCookie } from "./slices/auth";
import { addCookie, asyncLogin } from "./slices/login";

function App() {
    const cookie = new Cookies().get("user");
    const dispatch = useAppDispatch();
    useEffect(() => {
        if (!cookie) return;
        dispatch(addCookie());
    }, []);

    return (
        <div className="App">
            <GlobalStyle />
            <HeaderNavbar />
            <Routes>
                <Route path="/" element={<Main />} />
                <Route path="/artists" element={<Artists />} />
                <Route path="/archive" element={<Archive />} />
                <Route path="/inspiration/*" element={<Inspiration />} />
                <Route path="/login" element={<Login />} />
                <Route path="/register" element={<Register />} />
                <Route path="/test" element={<TestPage />} />
            </Routes>
            <Footer />
        </div>
    );
}

export default App;
