import { NavLink } from "react-router-dom";
import styled from "styled-components";
import { color } from "../assets/colors";
import { type ButtonDisabledProps } from "./HeaderMenuList";
import { useState, useCallback, useEffect } from "react";

interface Props extends ButtonDisabledProps {
    item: {
        id: number;
        to: string;
        link: string;
    };
}

const MenuList = ({ ButtonDisabled, item }: Props) => {
    const [menuActive, setMenuActive] = useState<boolean>(false);
    const { to, link } = item;

    const isOnActive = useCallback(() => {
        setMenuActive(true);
    }, []);
    const isOffActive = useCallback(() => {
        setMenuActive(false);
    }, []);
    useEffect(() => {
        isOnActive();
        isOffActive();
    }, [isOnActive, isOffActive]);
    return (
        <LiStyled onClick={isOnActive}>
            <LinkStyled
                to={to}
                onClick={ButtonDisabled}
                onMouseEnter={isOnActive}
                onMouseLeave={isOffActive}
                className={menuActive ? "active" : ""}
            >
                {link}
            </LinkStyled>
        </LiStyled>
    );
};

export default MenuList;

const LiStyled = styled.li`
    height: inherit;
    box-sizing: border-box;
`;
const LinkStyled = styled(NavLink)`
    position: relative;
    display: flex;
    align-items: center;
    height: inherit;
    font-size: 18px;
    color: ${color.black03};
    font-family: "Ms-M";
    padding: 0 17px;
    margin: 0 17px;
    box-sizing: border-box;

    &.active::before {
        content: "";
        position: absolute;
        left: 0;
        bottom: -1px;
        width: 100%;
        height: 3px;
        background: ${color.black06};
    }
`;
