import { useState, useCallback } from "react";
import styled from "styled-components";
import { useAppDispatch } from "../../hook";
import { addCookie, asyncLogin } from "../../slices/login";
import { asyncLogout } from "../../slices/logout";
import { Cookies } from "react-cookie";

const TestPage = () => {
    const [userId, setUserId] = useState("");
    const [userPw, setUserPw] = useState("");
    const dispatch = useAppDispatch();
    const arg = {
        id: userId,
        pw: userPw,
    };

    const login = (e: React.FormEvent<HTMLFormElement>) => {
        e.preventDefault();
        dispatch(asyncLogin(arg));
    };

    const onChangeId = useCallback((e: React.ChangeEvent<HTMLInputElement>) => {
        setUserId(e.target.value);
    }, []);

    const onChangePw = useCallback((e: React.ChangeEvent<HTMLInputElement>) => {
        setUserPw(e.target.value);
    }, []);

    const cookie = new Cookies();
    const onLogout = () => {
        dispatch(asyncLogout());
        cookie.remove("user", { path: "/" });
        dispatch(addCookie());
    };
    return (
        <>
            <FormStyled onSubmit={login}>
                <input type="text" onChange={onChangeId} />
                <input type="password" onChange={onChangePw} />
                <button type="submit">로그인</button>
            </FormStyled>
            <button onClick={onLogout}>로그아웃</button>
        </>
    );
};
export default TestPage;

const FormStyled = styled.form`
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 15px;
    input {
        border: 1px solid #000;
        width: 300px;
        height: 40px;
        box-sizing: border-box;
        padding: 0 20px;
    }
    button {
        width: 300px;
        height: 40px;
        background: #000;
        color: #fff;
    }
`;
