import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import axios from "axios";
import { Cookies } from "react-cookie";
const cookie = new Cookies();

interface InitialState {
    isLogged: boolean;
    isLoading: boolean;
    sessionId?: string;
}
const initialState: InitialState = {
    isLogged: false,
    isLoading: false,
    sessionId: "",
};

export const asyncAuth = createAsyncThunk(
    "userAuthSlice/asyncAuth",
    async (sessionId: "") => {
        try {
            const result = await axios.post(
                "http://localhost:5050/users/auth",
                {
                    sessionId: sessionId,
                },
                {
                    withCredentials: true,
                },
            );
            return result.data;
        } catch (error) {
            console.log(error);
        }
    },
);

const userAuthSlice = createSlice({
    name: "auth",
    initialState,
    reducers: {
        // addCookie: (res) => {
        //     res.cookie = cookie.get("user");
        // },
    },
    extraReducers(builder) {
        builder
            .addCase(asyncAuth.pending, (state, action) => {})
            .addCase(asyncAuth.fulfilled, (state, { payload: sessionId }) => {
                state.isLogged = true;
                state.sessionId = sessionId;
            })
            .addCase(asyncAuth.rejected, (state, action) => {
                console.log("error");
            });
    },
});

// export const { addCookie } = userAuthSlice.actions;
export default userAuthSlice.reducer;
